<template>
  <div class="class-setting">
    <h1>Setting</h1>

    <div class="setting-box">
      <h2>현재 출석 처리 시간</h2>
      <div class="current-time">
        <span>{{ currentStartTime }} - {{ currentEndTime }}</span>
        <small>(지각: {{ currentLateStartTime }} - {{ currentLateEndTime }})</small>
      </div>

      <div class="time-setting">
        <h3>출석 범위 설정</h3>
        <div class="time-input">
          <input type="number" v-model="newStartHour" min="0" max="23" /> :
          <input type="number" v-model="newStartMinute" min="0" max="59" /> ~
          <input type="number" v-model="newEndHour" min="0" max="23" /> :
          <input type="number" v-model="newEndMinute" min="0" max="59" />
        </div>

        <h3>지각 범위 설정</h3>
        <div class="late-setting">
          <label>
            <input type="radio" value="none" v-model="lateOption" /> 없음
          </label>
          <label>
            <input type="radio" value="5" v-model="lateOption" /> 5분
          </label>
          <label>
            <input type="radio" value="10" v-model="lateOption" /> 10분
          </label>
          <label>
            <input type="radio" value="custom" v-model="lateOption" /> 입력
            <input
                type="number"
                v-model="customLateMinutes"
                :disabled="lateOption !== 'custom'"
                min="0"
                max="59"
            /> 분
          </label>
        </div>
      </div>

      <div class="updated-time">
        <h2>변경된 출석 처리 시간</h2>
        <div>
          <span>{{ updatedStartTime }} - {{ updatedEndTime }}</span>
          <small>(지각: {{ updatedLateStartTime }} - {{ updatedLateEndTime }})</small>
        </div>
        <label>
          <input type="checkbox" v-model="rememberTime" /> 변경된 시간 기억하기
        </label>
      </div>

      <button @click="applySettings">적용하기</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ClassSetting",
  data() {
    return {
      // 초기 값
      currentStartTime: "13:00:00",
      currentEndTime: "13:10:00",
      currentLateStartTime: "13:10:00",
      currentLateEndTime: "13:20:00",

      // 새로운 설정 값
      newStartHour: 13,
      newStartMinute: 0,
      newEndHour: 13,
      newEndMinute: 10,

      lateOption: "none", // 지각 범위 옵션 ("none", "5", "10", "custom")
      customLateMinutes: 0, // 사용자 정의 지각 분

      // 업데이트된 값
      updatedStartTime: "13:00:00",
      updatedEndTime: "13:10:00",
      updatedLateStartTime: "",
      updatedLateEndTime: "",

      rememberTime: false, // 변경된 시간 기억하기 여부
    };
  },
  methods: {
    applySettings() {
      // 출석 시간 설정
      this.updatedStartTime = `${this.newStartHour.toString().padStart(2, "0")}:${this.newStartMinute
          .toString()
          .padStart(2, "0")}:00`;
      this.updatedEndTime = `${this.newEndHour.toString().padStart(2, "0")}:${this.newEndMinute
          .toString()
          .padStart(2, "0")}:00`;

      // 지각 시간 설정
      if (this.lateOption === "none") {
        this.updatedLateStartTime = "";
        this.updatedLateEndTime = "";
      } else {
        const lateMinutes =
            this.lateOption === "custom"
                ? this.customLateMinutes
                : parseInt(this.lateOption);
        const lateStartTime = new Date();
        lateStartTime.setHours(this.newEndHour);
        lateStartTime.setMinutes(this.newEndMinute + lateMinutes);

        const lateEndTime = new Date(lateStartTime);
        lateEndTime.setMinutes(lateStartTime.getMinutes() + 10);

        this.updatedLateStartTime = `${lateStartTime
            .getHours()
            .toString()
            .padStart(2, "0")}:${lateStartTime
            .getMinutes()
            .toString()
            .padStart(2, "0")}:00`;

        this.updatedLateEndTime = `${lateEndTime
            .getHours()
            .toString()
            .padStart(2, "0")}:${lateEndTime
            .getMinutes()
            .toString()
            .padStart(2, "0")}:00`;
      }

      // 변경된 시간 저장하기 여부 처리
      if (this.rememberTime) {
        localStorage.setItem("updatedStartTime", this.updatedStartTime);
        localStorage.setItem("updatedEndTime", this.updatedEndTime);
        localStorage.setItem("updatedLateStartTime", this.updatedLateStartTime);
        localStorage.setItem("updatedLateEndTime", this.updatedLateEndTime);
      }

      alert("변경 사항이 적용되었습니다!");
    },
  },
};
</script>

<style scoped>
.class-setting {
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
}

.setting-box {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.current-time,
.updated-time {
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.time-input {
  display: flex;
  gap: 5px;
  align-items: center;
}

.late-setting {
  display: flex;
  gap: 10px;
  align-items: center;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
</style>
