<template>
  <div class="attendance_button">
    <router-link v-if="userRole === 'student'" :to="{ name: 'QRscan' }" class="student_button">
      출석하기
    </router-link>
    <button v-else class="teacher_button" @click="handleButtonClick">
      출석하기
    </button>
  </div>
</template>

<script>
export default {
  name: "ClassButton",
  props: {
    userRole: {
      type: String,
      required: true, // "student" 또는 "teacher"
    },
    toggleQrDisplay: {
      type: Function,
      required: false, // QR 컴포넌트를 on/off하는 함수 전달
    },
  },
  methods: {
    handleButtonClick() {
      if (this.userRole === "teacher" && this.toggleQrDisplay) {
        this.toggleQrDisplay(true); // QR 컴포넌트를 활성화
      } else {
        alert("권한이 없습니다.");
      }
    },
  },
};
</script>

<style scoped>
.attendance_button {
  max-width: 900px;
  margin: 20px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.student_button {
  display: inline-block;
  width: auto; /* 버튼 크기 유동적으로 설정 */
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background: #335DA9;
  text-decoration: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.student_button:hover {
  background-color: #0056b3;
}

.teacher_button {
  display: inline-block;
  width: auto; /* 버튼 크기 유동적으로 설정 */
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background: #FF5733;
  text-decoration: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.teacher_button:hover {
  background-color: #C70039;
}
</style>
