<template>
  <div class="class_info">
    <div id="info_boxes" v-if="classData">
      <p>Class</p>
      <div class="info_box">
        <!-- 수업 정보 -->
        <div class="labels">
          <div>수업명</div>
          <div>교수명</div>
          <div>수업코드</div>
          <div>수업시간</div>
          <div>장소</div>
        </div>

        <div class="values">
          <div>{{ classData.lectureName }}</div>
          <div>{{ classData.teacherName }}</div>
          <div>{{ classData.lectureCode }}</div>
          <div>{{ classData.lectureTime }}</div>
          <div>{{ classData.lecturePlace }}</div>
        </div>
      </div>

      <div class="line"></div>

      <!-- 수업 및 주차 정보 -->
      <div class="info_box">
        <div class="labels">
          <div>주차</div>
          <div>수업날짜</div>
        </div>
        <div class="values">
          <div>{{ classData.week }}</div>
          <div>{{ classData.date }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ClassInfo",
  data() {
    return {
      classData: null, // 수업 데이터를 저장할 객체
    };
  },
  async created() {
    try {
      const response = await fetch("/data/class-info.json"); // 로컬 JSON 파일 경로
      if (response.ok) {
        this.classData = await response.json();
      } else {
        console.error("Failed to fetch class info");
      }
    } catch (error) {
      console.error("Error fetching class info:", error);
    }
  },
};
</script>

<style scoped>
.class_info {
  max-width: 900px;
  margin: 20px auto;
  border: 1px solid #D9D9D9;
  border-radius: 20px;
  padding: 20px;
  background: #fff;
}

@media (max-width: 942px) {
  .class_info {
    margin: 20px 5%;
  }
}

#info_boxes {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  margin: 10px;
}

#info_boxes > p {
  font-weight: 600;
  margin-bottom: 20px;
}

.info_box {
  display: flex;
  flex-direction: row; /* 동일한 행 배치 */
  justify-content: space-between;
  width: 100%; /* 부모 너비를 기준으로 */
  max-width: 600px; /* 최대 너비 제한 */
  gap: 20px;
  padding: 10px 0;
  box-sizing: border-box; /* 패딩 포함 */
}

.labels,
.values {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.labels > div,
.values > div {
  padding: 0;
  font-size: 15px;
}

.line {
  height: 1px;
  width: 100%; /* 부모 너비 기준 */
  max-width: 600px; /* 최대 너비 제한 */
  background-color: #ddd;
  margin: 20px auto;
}
</style>
