<template>
  <div class="qrscanner">
    <video id="video-preview" autoplay></video>
    <button @click="stopScanner">Stop Scanner</button>
    <p v-if="scannedResult">Scanned Result: {{ scannedResult }}</p>
  </div>
</template>

<script>
import { BrowserMultiFormatReader } from "@zxing/browser";

export default {
  name: "Qrscanner",
  data() {
    return {
      codeReader: null, // ZXing 코드 리더 객체
      scannedResult: null, // 스캔 결과 저장
    };
  },
  methods: {
    startScanner() {
      this.codeReader = new BrowserMultiFormatReader();
      const videoElement = document.getElementById("video-preview");

      this.codeReader.decodeOnceFromVideoDevice(undefined, videoElement)
          .then((result) => {
            this.scannedResult = result.text; // QR 코드 내용 저장
            alert(`Scanned Result: ${result.text}`);
          })
          .catch((err) => {
            console.error("Error while scanning QR code", err);
            alert("Error while scanning QR code. Please try again.");
          });
    },
    stopScanner() {
      if (this.codeReader) {
        this.codeReader.reset?.(); // 안전하게 reset 메서드 호출
        this.codeReader = null;
      }
    },
    goToAttendance() {
      this.$router.push({name: "Attendance"}); // Attendance 페이지로 이동
    },
  },
  mounted() {
    this.startScanner(); // 컴포넌트가 마운트되면 스캐너 시작
  },
  beforeUnmount() {
    this.stopScanner(); // 컴포넌트가 언마운트되면 스캐너 정지
  },
};
</script>

<style scoped>
.qrscanner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

#video-preview {
  width: 100%;
  max-width: 400px;
  border: 2px solid #333;
  margin-bottom: 10px;
}

button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
</style>
