<template>
  <div class="qr-display">
    <h1>QR Code Generator</h1>
    <div v-if="qrCodeVisible">
      <canvas id="qrCanvas"></canvas>
      <p>QR 코드가 생성되었습니다.</p>
    </div>
    <button @click="generateQrCode">QR 코드 생성</button>
  </div>
</template>

<script>
import { BrowserQRCodeSvgWriter } from "@zxing/browser";

export default {
  name: "QrDisplay",
  data() {
    return {
      qrCodeVisible: false, // QR 코드 표시 여부
    };
  },
  methods: {
    generateQrCode() {
      const qrData = "출석 확인용 QR 코드"; // QR 코드에 포함될 데이터
      const canvasElement = document.getElementById("qrCanvas");

      if (canvasElement) {
        const qrCodeWriter = new BrowserQRCodeSvgWriter();
        qrCodeWriter.writeToCanvas(canvasElement, qrData, 300, 300);
        this.qrCodeVisible = true; // QR 코드 표시 활성화
      } else {
        console.error("Canvas element not found");
      }
    },
  },
};
</script>

<style scoped>
.qr-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  max-width: 400px;
  margin: 0 auto;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

canvas {
  margin-top: 20px;
  border: 1px solid #333;
}
</style>
